import React, { useState } from 'react';
import { ActivityIndicator, FlatList, Text, View, Image, Button } from 'react-native';
import data from './assets/cards.json';
import { Audio } from 'expo-av';
import SmallClassImage from './SmallClassImage';
import SoundButton from './SoundButton';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';

const FlatListItemSeparator = () => {
  return (
    <View
      style={{
        height: 1,
        width: "100%",
        opacity: 0.25,
        backgroundColor: "#000000",
        marginRight: 8
      }}
    />
  );
}

const HomeScreen = ({ navigation }) => {
  return (
    <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}>
      <Text>Home Screen</Text>
      <Button
        title="Go to Details"
        onPress={() => navigation.navigate('Details')}
      />
    </View>
  );
}

const DetailsScreen = () => {
  return (
    <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}>
      <Text>Details Screen</Text>
    </View>
  )
}

const Stack = createStackNavigator();

const App = () => {
  return (
    <NavigationContainer>
      <Stack.Navigator>
        <Stack.Screen name="Home" component={HomeScreen} />
        <Stack.Screen name="Details" component={CardList} />
      </Stack.Navigator>
    </NavigationContainer>
  )
}

const CardList = () => {
  const mydata = data
    .filter(x => (x.collectible === true && x.type === 'MINION')).slice(0,100)
  const [isLoading, setLoading] = useState(true);

  return (
    <View style={{ flex: 1, padding: 24 }}>
    <FlatList
      data={mydata}
      renderItem={({ item }) => (
        <View style={{ flex: 1, flexDirection: 'row', alignItems: 'center'
        , justifyContent: 'space-between'
        , marginTop: 4, marginBottom: 4, marginRight: 8}}>
          <SmallClassImage cardClass={item.cardClass}/>
          <Text style={{flexGrow: 1}}>{item.name}</Text>
          <SoundButton title='Play' cardID={item.id} sound='P' />
          <View style={{padding: 4}} />
          <SoundButton title='Attack' cardID={item.id} sound='A' />
          <View style={{padding: 4}} />
          <SoundButton title='Death' cardID={item.id} sound='D' />
        </View>
      )}
      ItemSeparatorComponent={FlatListItemSeparator}
    />
    </View>
  );
}

export default App;
